@import "globals/zothacks-styles.scss";

.schedule-card {
  border-radius: 20px;
  z-index: 50;
  width: 55vw;
  background-color: #42384473;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
  color: #F6D4D4;
  grid-template-columns: 49% 2% 49%;
  grid-auto-flow: column;

  @media screen and (max-width: 800px) {
    display: flex;
    flex-direction: column;
    width: 85vw;
  }

  .schedule-card-divider-horizontal {
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width: 800px) {
      display: none;
    }

    div {
      height: 4px;
      width: 15%;
      border-radius: 5px;
      background-color: #F6D4D4;
      margin-bottom: 40px;
    }


  }

  .schedule-card-tooltip {
    position: relative;
  }

  .schedule-card-tooltip .schedule-card-tooltiptext {
    visibility: hidden;
    width: 400px;
    background-color: #f6d4d4dc;
    color: black;
    z-index: 1;
    top: 100%;
    text-align: center;
    border-radius: 6px;
    padding: 10px;
    position: absolute;
  }

  .schedule-card-tooltip .schedule-card-tooltiptext::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 10%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent #f6d4d4dc transparent;
  }

  .schedule-card-tooltip:hover .schedule-card-tooltiptext {
    visibility: visible;
    opacity: 1;
  }

  .schedule-card-section {

    .schedule-card-time-slot {
      width: 100%;
      // height: 60px;
      display: flex;
      justify-content: space-between;
      text-align: left;
      margin-bottom: 20px !important;

      div {
        height: 100%;
      }

      @media screen and (max-width: 450px) {
        h5 {
          font-size: 16px !important;
        }

        p {
          font-size: 12px;
        }
      }

      h5 {
        font-size: 21px;
        margin-bottom: 0;
      }

      p {
        color: white;
        margin-bottom: 0;
      }
    }

    & > div {
      margin: 30px;
    }
  }
}
